<template>
  <Row class="footer-menu">
    <Col
      :span="8"
      class="footer-menu-item"
      :class="{'active':active=='/messages'}"
      @click.native="activeUpdate('/messages')"
    >
      <Badge :count="unreadnum">
        <Icon type="md-reorder"></Icon>
        消息
      </Badge>
    </Col>
       <Col
      :span="8"
      class="footer-menu-item"
      :class="{'active':active=='/friends'}"
      @click.native="activeUpdate('/friends')"
    >
      <Badge >
        <Icon type="md-contacts"></Icon>
        好友
      </Badge>
    </Col> <Col
      :span="8"
      class="footer-menu-item"
      :class="{'active':active=='/user'}"
      @click.native="activeUpdate('/user')"
    >
      <Badge >
        <Icon type="md-person"></Icon>
        个人
      </Badge>
    </Col>
  </Row>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      active: "/messages",
    };
  },
  computed:{
    ...mapGetters({'unreadnum':'get_unreadnum'})
  },
  created() {
    this.active = this.$route.path;
  },
  methods: {
    searchdata(dataname){
      return this[dataname]
    },
    activeUpdate(active) {
      this.active = active;
      this.$router.push(active);
    }
  },
  watch: {
    $route(news, olds) {
      this.active = news.path;
    }
  }
};
</script>

<style scoped lang="scss">
.footer-menu {
  text-align: center;
  .footer-menu-item {
    cursor: pointer;
    transition: all 0.2s;
    &.active {
      border-top: 5px solid #1390f7;
      color: #1390f7;
    }
  }
}
</style>
